<template>
  <div class="edit">
    <!-- <h1>This is Admin/Edit/{{ $route.params.id }}</h1> -->
    <div v-if="show" id="watch-example">
      <p ref="title">
        Ask a yes/no question:
        <input v-model="question" />
      </p>
      <p>{{ answer }}</p>
    </div>
    <div :class="{ classB: classB }">table--{{ getAnswer("") }}</div>
  </div>
</template>

<script>
export default {
  name: "edit",
  data() {
    return {
      show: false,
      classB: "",
      question: "",
      answer: "I cannot give you an answer until you ask a question!"
    };
  },
  mounted() {
    this.getList()
  },
  watch: {
    question: function(newQuestion, oldQuestion) {
      this.answer = "Waiting for you to stop typing...";
    }
  },
  methods: {
    getAnswer(data) {
      let toString = Object.prototype.toString;
      let dataType = toString.call(data).replace(/\[object\s(.+)\]/, "$1");
      return dataType;
      this.getAnswer=`the network is so terrible`
      let obj=new FormData()
      var xml=new xmlHttpRequest()
      xml.open('get','/api/admin/rail-respection')
      xml.send();
      xml.onreadystatechange=function(){
          if(xml.status===200 && xml.readyState===4){
              console.log(responseText)
              let res=responseText.list
              for(let i=0;i<res.length;i++){
                  this.listQuery.push({
                    name:res[i].name,
                    password:res[i].password,
                    keyWords:res[i].keyWords
              })
              }
          }
      }
    },
    getList() {
    //   console.log(this.$refs.title.textContent)
      setTimeout(() => {
          console.log('定时器')
      }, 1000);
    //   console.log(this.$attrs); 
    //   document.cookie='myname=laihuamin';//cookie写入和获取 
    //   console.log(document.cookie);
    
    }
  }
};
</script>
<style>
.nav {
  width: 100%;
  height: 60px;
  background-color: #ffddee;
  font-weight: 500;
}
</style>



